'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useToast } from '@/hooks/use-toast';
import { formSchema, Template } from '@/lib/template-utils';
import { saveTemplateAction } from '@/actions/template-actions';
import { BlockEditor } from './block-editor';
import { CertificateEditor } from './certificate-editor';

interface TemplateEditorClientProps {
  templateData?: Template | null;
}

export function TemplateEditorClient({ templateData }: TemplateEditorClientProps) {
  const router = useRouter();
  const { toast } = useToast();
  const [isSaving, setIsSaving] = useState(false);

  const defaultValues = {
    templateName: templateData?.nombre || '',
    emailSubject: templateData?.asunto_predeterminado || '',
    categoria: templateData?.categoria || 'Otro',
    blocks: templateData?.contenido || [],
  };

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues,
  });

  const categoria = form.watch('categoria');

  useEffect(() => {
    form.reset(defaultValues);
  }, [templateData, form]);

  async function onSubmit(values: z.infer<typeof formSchema>) {
    setIsSaving(true);
    try {
      const result = await saveTemplateAction({
        id: templateData?.id_plantilla,
        nombre: values.templateName,
        asunto_predeterminado: values.emailSubject,
        contenido: values.blocks, // Aquí necesitaríamos ajustar qué se guarda para el certificado
        categoria: values.categoria,
      });

      if (result.success) {
        toast({
          title: 'Plantilla guardada',
          description: result.message,
        });
        router.push('/templates');
        router.refresh();
      } else {
        throw new Error(result.message);
      }
    } catch (error) {
      toast({
        title: 'Error al guardar',
        description: (error as Error).message,
        variant: 'destructive',
      });
    } finally {
      setIsSaving(false);
    }
  }

  return (
    <DndProvider backend={HTML5Backend}>
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-headline font-bold">Editor de Plantillas</h1>
              <p className="text-muted-foreground">
                Diseña tus plantillas, ya sea para campañas, eventos o certificados.
              </p>
            </div>
            <Button type="submit" disabled={isSaving}>
              {isSaving ? 'Guardando...' : 'Guardar Plantilla'}
            </Button>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            <div className="lg:col-span-1 space-y-6">
              <Card>
                <CardHeader>
                  <CardTitle>1. Detalles de la Plantilla</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <FormField
                    control={form.control}
                    name="templateName"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Nombre de la Plantilla</FormLabel>
                        <FormControl>
                          <Input placeholder="Ej: Bienvenida a nuevos usuarios" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={form.control}
                    name="emailSubject"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Asunto del Correo</FormLabel>
                        <FormControl>
                          <Input placeholder="¡Bienvenido a bordo!" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={form.control}
                    name="categoria"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Categoría</FormLabel>
                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                          <FormControl>
                            <SelectTrigger>
                              <SelectValue placeholder="Selecciona una categoría" />
                            </SelectTrigger>
                          </FormControl>
                          <SelectContent>
                            <SelectItem value="Evento">Evento</SelectItem>
                            <SelectItem value="Encuesta">Encuesta</SelectItem>
                            <SelectItem value="Campaña">Campaña</SelectItem>
                            <SelectItem value="Notificación">Notificación</SelectItem>
                            <SelectItem value="Certificado">Certificado</SelectItem>
                            <SelectItem value="Otro">Otro</SelectItem>
                          </SelectContent>
                        </Select>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                </CardContent>
              </Card>
            </div>

            <div className="lg:col-span-2">
              {categoria === 'Certificado' ? (
                <Card>
                  <CardHeader>
                    <CardTitle>2. Diseño del Certificado</CardTitle>
                    <CardDescription>Diseña el certificado visualmente. Este contenido se guardará en la plantilla.</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <CertificateEditor />
                  </CardContent>
                </Card>
              ) : (
                <Card>
                  <CardHeader>
                    <CardTitle>2. Contenido de la Plantilla</CardTitle>
                    <CardDescription>Arrastra y suelta bloques para construir tu correo.</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <FormField
                      control={form.control}
                      name="blocks"
                      render={({ field }) => (
                        <BlockEditor value={field.value} onChange={field.onChange} />
                      )}
                    />
                  </CardContent>
                </Card>
              )}
            </div>
          </div>
        </form>
      </Form>
    </DndProvider>
  );
}
