'use client';

import { useEffect, useRef, useState } from 'react';
import { fabric } from 'fabric';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { ScrollArea } from '@/components/ui/scroll-area';

interface CertificateEditorProps {
  value?: string;
  onChange: (value: string) => void;
}

export function CertificateEditor({ value, onChange }: CertificateEditorProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const fabricCanvasRef = useRef<fabric.Canvas | null>(null);
  const [backgroundImage, setBackgroundImage] = useState<string>('');

  useEffect(() => {
    if (!canvasRef.current) return;
    const canvas = new fabric.Canvas(canvasRef.current, { width: 800, height: 600 });
    fabricCanvasRef.current = canvas;

    if (value) {
      try {
        const json = JSON.parse(value);
        setBackgroundImage(json.backgroundImage || '');
        canvas.loadFromJSON(json, () => {
          canvas.renderAll();
        });
      } catch (e) {
        console.error('Error parsing certificate JSON:', e);
      }
    }

    const handleModified = () => {
      if (fabricCanvasRef.current) {
        const json = fabricCanvasRef.current.toJSON(['backgroundImage']);
        onChange(JSON.stringify(json));
      }
    };

    canvas.on('object:modified', handleModified);
    canvas.on('object:added', handleModified);
    canvas.on('object:removed', handleModified);

    return () => {
      canvas.dispose();
      fabricCanvasRef.current = null;
    };
  }, [value, onChange]);

  useEffect(() => {
    const canvas = fabricCanvasRef.current;
    if (!canvas) return;

    if (backgroundImage) {
      fabric.Image.fromURL(backgroundImage, (img) => {
        canvas.setBackgroundImage(img, canvas.renderAll.bind(canvas), {
          scaleX: canvas.width! / img.width!,
          scaleY: canvas.height! / img.height!,
        });
        // Store background image URL on canvas object to persist it
        (canvas as any).backgroundImage = backgroundImage;
      }, { crossOrigin: 'anonymous' });
    } else {
      canvas.setBackgroundImage(null, canvas.renderAll.bind(canvas));
    }
  }, [backgroundImage]);

  const addText = (text: string) => {
    const canvas = fabricCanvasRef.current;
    if (!canvas) return;
    const textObj = new fabric.Textbox(text, {
      left: 100,
      top: 100,
      fill: '#000000',
      fontSize: 24,
      fontFamily: 'Arial',
      width: 200,
    });
    canvas.add(textObj);
    canvas.setActiveObject(textObj);
  };

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (event) => {
        setBackgroundImage(event.target?.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <div className="grid grid-cols-1 md:grid-cols-[1fr_300px] gap-4">
      <div className="p-2 border rounded-md bg-muted/40">
        <canvas ref={canvasRef} />
      </div>
      <div className="space-y-4">
        <Tabs defaultValue="background">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="background">Fondo</TabsTrigger>
            <TabsTrigger value="elements">Elementos</TabsTrigger>
          </TabsList>
          <TabsContent value="background" className="p-1">
            <div className="space-y-2">
              <Label htmlFor="bg-image">Imagen de Fondo</Label>
              <Input id="bg-image" type="file" accept="image/*" onChange={handleImageUpload} />
              {backgroundImage && <img src={backgroundImage} alt="Preview" className="mt-2 rounded-md border" />} 
            </div>
          </TabsContent>
          <TabsContent value="elements" className="p-1">
            <div className="space-y-2">
              <p className="font-medium">Campos Dinámicos</p>
              <Button variant="outline" size="sm" className="w-full justify-start" onClick={() => addText('{{nombre_asistente}}')}>Nombre del Asistente</Button>
              <Button variant="outline" size="sm" className="w-full justify-start" onClick={() => addText('{{nombre_evento}}')}>Nombre del Evento</Button>
              <Button variant="outline" size="sm" className="w-full justify-start" onClick={() => addText('{{fecha_evento}}')}>Fecha del Evento</Button>
              <Button variant="outline" size="sm" className="w-full justify-start" onClick={() => addText('{{codigo_qr}}')}>Código QR</Button>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
